import React, { useEffect } from "react";
import useStyles from "./useStyles";
import Grid from "@material-ui/core/Grid";
import PokeCard from "components/PokeCard";
import { connect } from "react-redux";
import { getPokeNext, getPokePrevious } from "redux/modules/pokemon";

const PokeGrid = ({
  loading,
  stats: { leftLimit, rightLimit },
  pokemon,
  getPokeNext,
  getPokePrevious,
}) => {
  useEffect(() => {
    getPokeNext();
  }, [getPokeNext]);
  return (
    <div>
      <button disabled={loading || leftLimit} onClick={getPokePrevious}>
        Previous
      </button>
      <button disabled={loading || rightLimit} onClick={getPokeNext}>
        Next
      </button>
      <Grid container>
        {pokemon.results.map(({ name, weight, height, id, sprites }) => (
          <PokeCard key={name} name={name} imgUrl={sprites.front_default} />
        ))}
      </Grid>
    </div>
  );
};

const mapStateToProps = ({ pokemon, loading, error, stats }) => ({
  pokemon,
  loading,
  error,
  stats,
});

const mapDispatchToProps = (dispatch) => ({
  getPokeNext: () => dispatch(getPokeNext()),
  getPokePrevious: () => dispatch(getPokePrevious()),
});

export default connect(mapStateToProps, mapDispatchToProps)(PokeGrid);
